import React from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
import content from "../content";
import "./Work.css";


export default function Work() {
  return (
    <div
      className="min-h-screen flex justify-center items-center flex-col"
      style={{ background: "#fefefe" }} id="mywork"
    >
      <h1 className="text-5xl font-dosis font-bold text-gray-600 mb-10 p-10">
        {content.work.title}
      </h1>
      <div className="card-wrapper flex flex-wrap flex-col md:flex-row justify-center items-center mb-20">

        <div className="flex flex-wrap justify-center card bg-white shadow-xl ml-2 rounded-xl m-5 transform hover:scale-105 ease-in-out duration-700">
          <div className="p-4 md:w-1/1 wrapper-work-img">
            <LazyLoadImage
            src="./images/travel-page.png" className="work-img"
              style={{ height: "300px", width: "220px" }}
            />
          </div>
          <div className="p-4 md:w-1/2 max-w-sm wrapper-content-work">
            <div>
              <h1 className="text-4xl mb-5 font-bold">TRVL</h1>
            </div>
            <p className="text-2xl text-gray-500 font-normal mb-10 pb-6">
            TRVL website, this project was created just to learn react
            </p>
            <div className="mt-10 flex flex-row justify-between pt-10 w-content-git">
              <LazyLoadImage src="./images/github-black.svg" className="w-10 h-10" />
              <span className="text-lg mt-4 text-black font-semibold">#ReactJs | #Sass</span>
              <span className="text-xl font-semibold mt-4 text-black">
              <a href="https://github.com/agustinartf13/reactjs-trvlwebsite">
              Details
              </a>
              </span>
            </div>
          </div>
        </div>

        <div className="flex flex-wrap justify-center card bg-white shadow-xl rounded-xl m-5 transform hover:scale-105 ease-in-out duration-700">
          <div className="p-4 md:w-1/1">
            <LazyLoadImage
              src="./images/funfuncation.png"
              style={{ height: "300px", width: "220px" }}
            />
          </div>
          <div className="p-4 md:w-1/2 max-w-sm wrapper-content-work">
            <div>
              <h1 className="text-4xl mb-5 font-bold">FunFuncation</h1>
            </div>
            <p className="text-2xl font-normal text-gray-500 mb-10 pb-6">
            FunFuncation website, this project was created just to learn react
            </p>
            <div className="mt-10 flex flex-row justify-between pt-10 w-content-git">
              <LazyLoadImage src="./images/github-black.svg" className="w-10 h-10" />
              <span className="text-lg mt-4 text-black font-semibold">#ReactJs | #Sass</span>
              <span className="text-lg mt-4 text-black font-semibold"><a href="">
              Details
              </a></span>
            </div>
          </div>
        </div>

        <div className="flex flex-wrap justify-center card bg-white shadow-xl rounded-xl m-5 transform hover:scale-105 ease-in-out duration-700">
          <div className="p-4 md:w-1/1">
            <LazyLoadImage
              src="./images/airbnb.png"
              style={{ height: "300px", width: "220px" }}
            />
          </div>
          <div className="p-4 md:w-1/2 max-w-sm wrapper-content-work">
            <div>
              <h1 className="text-4xl mb-5 font-bold">Airbnb</h1>
            </div>
            <p className="text-2xl mb-10 pb-6 font-normal text-gray-500">
            Airbnb website, this project was created just to learn react
            </p>
            <div className="mt-10 flex flex-row justify-between pt-10 w-content-git">
              <LazyLoadImage src="./images/github-black.svg" className="w-10 h-10" />
              <span className="text-lg mt-4 text-black font-semibold">#ReactJs | #firebase</span>
              <span className="text-lg mt-4 text-black font-semibold"><a href="https://github.com/agustinartf13/reactjs-trvlwebsite">
              Details
              </a></span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
