import React, { useState, useEffect } from "react";
import "./Navigation.css";
import { Link as ScrollLink } from "react-scroll";
import { FaBars, FaTimes } from "react-icons/fa";
import { IconContext } from 'react-icons/lib';
import content from "../content";
import { LazyLoadImage } from "react-lazy-load-image-component";

function Navbar() {
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    showButton();
  }, []);

  window.addEventListener("reset", showButton);

  return (
    <IconContext.Provider value={{ colors: "#fff" }}>
      <div className="navbar">
        <div className="navbar-container container">
          <ScrollLink to="myheader" smooth={true}  className="navbar-logo pb-3" onClick={closeMobileMenu}>
          <LazyLoadImage className="navbar-icon" src={content.nav.img} />
          </ScrollLink>
          {/* humberger menu mobile */}
          <div className="menu-icon justify-center items-center" onClick={handleClick}>
            {click ? <FaTimes /> : <FaBars />}
          </div>

          <ul className={` cursor-pointer ${click ? "nav-menu active" : "nav-menu"}`}>
            <li className="nav-item">
              <ScrollLink to="mywork" smooth={true} className="nav-links" onClick={closeMobileMenu}>
                Work
              </ScrollLink>
            </li>
            <li className="nav-item">
              <ScrollLink to="myexperience" smooth={true} className="nav-links"  onClick={closeMobileMenu}>
                Experience
              </ScrollLink>
            </li>
            <li className="nav-item">
              <ScrollLink to="myabout" smooth={true} className="nav-links" onClick={closeMobileMenu}>
                About
              </ScrollLink>
            </li>
            <li className="nav-item">
              <ScrollLink to="mycontact" smooth={true}className="nav-links" onClick={closeMobileMenu}>
                Contact
              </ScrollLink>
            </li>
          </ul>
        </div>
      </div>
    </IconContext.Provider>
  );
}

export default Navbar;
