import React, { useEffect, useState } from "react";
import content from "../content";
import Typical from "react-typical";
import { Link as ScrollLink } from "react-scroll";
import "react-lazy-load-image-component/src/effects/blur.css";
import useStartAnimation from "../Hook/useStartAnimation";
import './Header.css';

export default function Header() {
  const transition = (duration) =>
    `transition duration-${duration} ease-in-out`;
  const styleTranslate = "translate-y-10 opacity-0";
  const animated = useStartAnimation();
  return (
    <div
      className="min-h-screen flex justify-center items-center"
      style={{ background: "#091c29" }} id="myheader"
    >
      <div className="">
        <div className="text-white font-dosis text-center flex-column">
          <h2
            className={`${
              animated ? "" : "translate-y-10 opacity-0"
            } transform transition duration-2000 ease-in-out font-bold text-4xl`}
          >
            {content.header.text[0]}
            <br />
            {content.header.text[1]}
          </h2>
          <h1
            className={`${
              animated ? "" : "translate-y-10 opacity-0"
            } transform transition duration-2000 ease-in-out pt-3 text-2xl font-semibold text-gray-400 mb-10`}
          >
            {content.header.text[2]}{" "}
            <Typical
              steps={content.header.typical}
              loop={Infinity}
              className="inline-block"
            />
          </h1>
          <ScrollLink to="myabout" smooth={true}>
            <button
              className={` animate-bounce bg-indigo-500 px-10 py-3 btn-discover text-lg font-semibold uppercase text-white rounded-lg hover:bg-indigo-300 transform  ${
                animated ? "translate-y-0" : styleTranslate
              } ${transition(3500)}`}
            >
              {content.header.btnText}
            </button>
          </ScrollLink>
        </div>
      </div>
    </div>
  );
}
