import React from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
import content from "../content";
import "./Stack.css";

function Stack() {
  return (
    <div
      className="min-h-screen flex items-center justify-center flex-col"
      style={{
        background: "#eee",
      }} id="myexperience"
    >
      <div className="p-10 justify-center text-">
        <h1 className="text-5xl font-dosis font-bold text-center mb-10 text-gray-600 pb-10">
          {content.stack.title}
        </h1>
        <div className="flex flex-wrap justify-center">
          {content.stack.tech.map((tech, index) => (
            <div
              key={index}
              className={`h-40 w-40 bg-white shadow-xl ml-2 rounded-xl flex justify-center items-center p-6 m-2  ${
                index % 2 !== 0 ? "animate-bounce" : "animate-bounceFirst"
              }`}
            >
              <div className="flex flex-col">
                <LazyLoadImage
                  className="mb-5"
                  effect="blur"
                  src={tech.img}
                  alt={tech.alt}
                />

                <h4 className="absolute bg-gray-200 px-4 rounded-full text-black left-5 bottom-4 text-center">
                  {tech.level}
                </h4>
              </div>
            </div>
          ))}
        </div>
      </div>
      <p className="mt-10 mb-10 w-11/12 md:max-w-xl text-center inline-block text-xl md:text-2xl font-dosis ">
        {content.stack.desc}.
      </p>
    </div>
  );
}

export default Stack;
